.jumbotron {
	background-position: center;
	margin-bottom: 0px;
    text-align: center;
}

.jumbotron h1 {
	font-family: 'Montserrat', sans-serif;
	font-size: 65px;
	text-transform: uppercase;
	font-weight: 900;
	color: #e7283a;
}

.jumbotron h4 {
	font-family: 'Montserrat', sans-serif;
	font-size: 24px;
	font-weight: 500;
	text-transform: uppercase;
}
.allCards {
    padding: 10px;
    margin-bottom: 10px;
    border: none;
    text-align: center;
}

.allCards img {
    margin: 5px;
    padding: 5px;
    border: 1px solid rgba(0, 0, 0, 0.25);
    height: 150px;
    width: 150px;
    background-color: white;
    
}
.cardContainer {
    margin-bottom: 50px;
    padding: 0px 300px; 
}

.allCards {
    float: left;
    width: 25%;
}

.gameMessage {
    text-align: center;
    max-width: 100%;
    align-content: center
}

.gameMessage p {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    background-color: black;
    color: white;
    padding: 20px 0;
    margin-bottom: 0px;
}

.gameScores {
    text-align: center;
    max-width: 100%;
    align-content: center;
    margin-bottom: 10px;
}
.gameScores p {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    background-color:#e7283a;
    color: white;
    padding: 20px 0;
    margin-bottom: 25px;
    font-size: 16px;
}
